import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class ReservationService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  InputSettings(viewId: any) {
    return this.get(`AJAX/InputSettings/${viewId}`);
  }

  SubmitInputsSettings(data: any) {
    return this.post("AJAX/InputSettings", data);
  }

  returnBranchesByEduCompId<T>(EduCompId: any) {
    return this.get<T>(`AJAX/returnBranchesByEduCompId/${EduCompId}`);
  }

  GetAvaliableDatesByBranchId(
    BranchId: any,
    MustHaveStudents: boolean,
    DontShowMaxed: boolean
  ) {
    return this.get(
      `${`AJAX/GetAvaliableDatesByBranchId/${BranchId}`}/${MustHaveStudents}/${DontShowMaxed}`
    );
  }

  returnCities<T>() {
    return this.get<T>("AJAX/returnCities");
  }

  getArea<T>() {
    return this.get<T>("AJAX/returnArea");
  }

  returnStatus<T>() {
    return this.get<T>("AJAX/returnStatus");
  }

  getReservationsList<T, TE>(data: T) {
    return this.post<TE>("AJAX/returnReservations", data);
  }

  ReservationDetails(id: any) {
    return this.get(`AJAX/ReservationDetails/${id}`);
  }

  getReservationEdit<T>(id: any, EduCompId: any) {
    return this.get<T>(`AJAX/ReservationEdit/${id}/${EduCompId}`);
  }

  SubmitReservationEdit(data: any) {
    return this.post("AJAX/ReservationEdit", data);
  }

  GetStudentStatus(Id: any, EduCompId: any) {
    return this.get(`AJAX/ReservationStatus/${Id}/${EduCompId}`);
  }

  SubmitReservationStatus(data: any) {
    return this.post("AJAX/ReservationStatus", data);
  }

  ReservationStatusPaging<T>(page: any, EduCompUserId: any) {
    return this.get<T>(`AJAX/ReservationStatusPaging/${page}/${EduCompUserId}`);
  }

  getReasonsByStatusId<T>(StatusId: any) {
    return this.get<T>(`Admin/returnReasonsByStatusId/${StatusId}`);
  }

  updateStudentCustomDiscount(body: any) {
    return this.post("CustomDiscountsUsers/SaveCustomDiscountUser", body);
  }

  getStudentCustomDiscount<T>(userId: any) {
    return this.get<T>(
      `CustomDiscountsUsers/returnUserCustomDiscountDetails/${userId}`
    );
  }

  getCustomDiscount<T>(EduCompId: any) {
    return this.get<T>(
      `CustomDiscountsUsers/returnCustomDiscountsByEduCompId/${EduCompId}`
    );
  }

  getStudentCustomDiscountsList<T>(userId: any) {
    return this.get<T>(
      `CustomDiscountsUsers/returnUserCustomDiscountsByUserId/${userId}`
    );
  }

  getCustomReservation(EduCompId: any) {
    return this.get("Reservations/CustomReservation/" + EduCompId);
  }

  returnBranchesByEducTypeId<T>(typeId: any, EduCompId: any) {
    return this.get<T>(
      `Reservations/returnCenterBranchesByEducationalTypeId/${typeId}/${EduCompId}`
    );
  }

  addReservationDate<T, TV>(data: T) {
    return this.post<TV>(`reservation/add-date`, data);
  }

  getAllReservationDate<T>(page: number) {
    return this.get<T>(`reservation/get-all/${page}`);
  }

  downloadTemplate() {
    return this.get("reservation/download-date-template");
  }

  getAvailableGroups(teacherId: any, EduCompId: any) {
    return this.get(
      `Reservations/returnAvailableStudyingGroupsByTeacherId/${teacherId}/${EduCompId}`
    );
  }

  CompletaReservation(data: any) {
    return this.get(
      `Reservations/reservationPaymentAndAddStudentToGroup/${data.groupId}/${data.teacherUserId}/${data.eduCompId}`
    );
  }

  getReservationInfo<T>(EduCompId: number) {
    return this.get<T>(`Reservations/returnReservationInfo/${EduCompId}`);
  }

  checkUserBalance(EduCompId: any, teacherId: any) {
    return this.get(
      `Reservations/isUserHasBalanceForCustomTeacher/${EduCompId}/${teacherId}`
    );
  }
}
