import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class AnnouncementService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  addAnnouncement(data: any) {
    return this.post("AJAX/AddAnnouncement", data);
  }

  getAnnounceById(id: any) {
    return this.get(`Announcement/get-announce-by-Id/${id}`);
  }

  getAnnouncements<T>(teacherUserID: number) {
    return this.get<T>(`Announcement/GetAnnouncement/${teacherUserID}`);
  }

  getAllAnnouncements<T>(teacherUserID: number) {
    return this.get<T>(`Announcement/GetAllAnnouncement/${teacherUserID}`);
  }
}
