import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EducationalService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  //Get Educational Entities
  getEducationalEntities() {
    return this.get("Admin/getEducationalEntities");
  }

  //Add New Educational Entities
  addNewEducationalEntity(data: any) {
    return this.post("Admin/addNewEducationalEntity", data);
  }

  //Remove Educational Entities
  removeEducationalEntity(id: any) {
    return this.post("Admin/removeEducationalEntity/" + id, null);
  }

  ///////////////////////////////////////////////////////////////

  //Get Educationa Stage
  getEducationaStages() {
    return this.get("Admin/getEducationaStages");
  }

  //Add New Educational Stage
  AddEducationalStage(countryID: any, stageName: any) {
    //stageID
    return this.post(
      `Admin/AddEducationalStage/${countryID}/${stageName}`,
      null
    );
  }

  //Edit Educational Stage
  EditEducationalStage(StageID: any, countryID: any, stageName: any) {
    //stageID
    return this.post(
      `Admin/EditEducationalStage/${StageID}/${countryID}/${stageName}`,
      null
    );
  }

  //Delete Educational Stage
  DeleteEducationalStage(StageID: any) {
    //stageID
    return this.post(`Admin/RemoveEducationalStage/${StageID}`, null);
  }

  //return Stages By teacherId
  getStageByTeacherId(teacherId: any) {
    return this.get(`AJAX/returnStages/${teacherId}`);
  }

  ///////////////////////////////////////////////////////////////

  //Get Educationa Years
  getEducationalYear() {
    return this.get("Admin/educationalYear");
  }

  //Add New Educational Year
  AddEducationalYear(data: any) {
    //eduYearID, stageID, eduYearName
    return this.post("Admin/educationalYear", data);
  }

  //delete Educational Year
  deleteEducationalYear(id: any) {
    return this.delete(`Admin/educationalYear/${id}`);
  }

  //Get Educationa  Stage By CountryID
  getEducationalStageByCountryID<T>(countryID: any) {
    return this.get<T>(`Admin/stages/${countryID}`);
  }
  //return Years BY stageId

  returnYears(stageId: any) {
    return this.get(`AJAX/GetEducationalYearOfStudents/${stageId}`);
  }

  //return Teacher Subjects   //{ "yearId" : 36, "teacherId" : 17}
  returnTeacherSubjects(data: any) {
    return this.post(`AJAX/returnSubjectsByYearTeacherIDs`, data);
  }

  //return Units BY SubjectId
  GetUnits(SubjectId: any) {
    return this.get("AJAX/GetUnits/" + SubjectId);
  }

  //return Lessons BY unitId
  GetLessons(unitId: any) {
    return this.get(`AJAX/GetLessons/${unitId}`);
  }

  //return Topics BY LessonId
  GetTopics(LessonId: any) {
    return this.get("AJAX/GetTopics/" + LessonId);
  }

  //get Year by StageID
  getYearbyStageID(stageID: any) {
    return this.get(`Admin/educationalYear/${stageID}`);
  }

  matBySbjectURL = "codes/returnMaterialsByMainSubjectTeacherId/";
  GetMaterialsBySubject(subjectId: any, teacherId: any, EduCompId: any) {
    return this.get(
      `${`codes/returnMaterialsByMainSubjectTeacherId/${subjectId}`}/${teacherId}/${EduCompId}`
    );
  }
}
